

// const use_root = { root: true }


const debug = 1
const preview = 2
const release = 3


const state = {
    profiles: [debug, preview, release],
    active: preview,
};

const getters = {
    debug(state) {
        return (state.active <= debug)
    },

    preview(state) {
        return (state.active <= preview)
    },

    release(state) {
        return (state.active <= release)
    }
}

const mutations = {
};

const actions = {
};


export default {
    name: "module_develop",
    namespaced: true,
    state, mutations, actions, getters
}
