<template>
  <div class="open-with-list">
    <div
      class="open-with-item"
      v-for="item in apps"
      :key="item.id"
      :title="item.title"
      @click="handleClickItem(item)"
    >
      <img class="icon" :src="item.icon" />
    </div>
  </div>
</template>

<script>
export default {
  name: "home-local-repository-open-with-list",

  computed: {
    apps() {
      return this.$store.getters["executable/openWithList"];
    },
  },

  data() {
    return {
      //   apps: [],
    };
  },

  methods: {
    handleClickItem(item) {
      let name = "open-with";
      let executable = item;
      this.$emit("action", { name, executable });
    },
  },
};
</script>

<style scoped>
.icon {
  width: 32px;
  height: 32px;
  padding: 3px;
}

.icon:hover {
  width: 36px;
  height: 36px;
}

.open-with-list {
  display: flex;
  flex-direction: row;
  filter: grayscale(0.8) opacity(0.1);
  min-height: 56px;
}

.open-with-list:hover {
  filter: grayscale(0) opacity(1);
}

.open-with-item {
  cursor: pointer;
}
</style>
