

const use_root = { root: true }


const state = {
    all: [],
    pageSize: 16,
    pageIndex: 0,
    currentPageItems: [],
    repositories: [],
};

const getters = {

    all(state) {
        return state.all
    },

    list(state) {
        return state.repositories
    },

}

const mutations = {

    handle_repositories(state, vo) {
        if (vo == null) { return }
        let repositories = vo.repositories
        if (repositories == null) { return }
        state.repositories = repositories
    }

};

const actions = {
    fetch(context) {
        let method = 'GET'
        let type = 'remote-repositories'
        let task = context.dispatch("rest/execute", { method, type }, use_root)
        task.then((r) => {
            let vo = r.data;
            context.commit('handle_repositories', vo)
        })
        return task;
    },
};



export default {
    name: "module_remote_repository",
    namespaced: true,
    state, mutations, actions, getters
}
