

const use_root = { root: true }


const state = {
    items: []
};

const getters = {
    list(state) {
        return state.items
    }
}

const mutations = {

    handle_items(state, vo) {
        if (vo == null) { return }
        let  items  = vo. items 
        if ( items  == null) { return }
        state. items =  items
    }

};

const actions = {
    fetch(context) {
        let method = 'GET'
        let type = 'intents'
        let task = context.dispatch("rest/execute", { method, type }, use_root)
        task.then((r) => {
            let vo = r.data;
            context.commit('handle_example', vo)
        })
        return task;
    },
};


export default {
    name: "module_example",
    namespaced: true,
    state, mutations, actions, getters
}
