<template>
  <HomeFrame :config="theFrameConfig" @action="handleAction">
    <div class="pager-box">
      <el-pagination
        v-model:current-page="currentPage"
        v-model:page-size="pageSize"
        :page-sizes="[5, 10, 20, 50, 100, 200, 500]"
        :background="background"
        layout="total, sizes, prev, pager, next"
        :total="theCountItemsTotal"
        @size-change="handleSizeChange"
        @current-change="handleCurrentChange"
      />
    </div>
    <MyRepos :data="theItems" />
  </HomeFrame>
</template>

<script>
import MyRepos from "./item-list.vue";

export default {
  name: "home-local-repository-index",
  components: { MyRepos },

  computed: {
    theFrameConfig() {
      return {
        pager: false,
        home: true,
        nav: true,
        search: true,
        searchCategory: "",
      };
    },

    theCountItemsTotal() {
      return this.$store.getters["local_repository/total"];
    },

    theItems() {
      return this.$store.getters["local_repository/currentPageItems"];
    },
  },

  data() {
    return {
      pageSize: 1,
      currentPage: 1,
      background: "",
    };
  },

  methods: {
    handleAction(p) {
      let name = p.name;
      if (name == "refresh") {
        this.fetch();
      } else if (name == "add") {
        this.add();
      } else if (name == "applyFilter") {
        this.applyFilter(p.value);
      } else {
        console.warn("unsupported action: " + name);
      }
    },

    fetch() {
      this.$store.dispatch("local_repository/fetch");
    },

    initOpenWithList() {
      this.$store.dispatch("executable/init");
    },

    add() {
      this.$router.push("/import-local-repo");
    },

    handleSizeChange(value) {
      let size = value;
      this.$store.commit("local_repository/update", { size });
    },

    handleCurrentChange(value) {
      let index = value - 1;
      this.$store.commit("local_repository/update", { index });
    },

    applyFilter(filter) {
      this.$store.commit("local_repository/update", { filter });
    },

    pageViewToModel() {
      let index = this.currentPage - 1;
      let size = this.pageSize;
      this.$store.commit("local_repository/update", { index, size });
    },

    pageModelToView() {
      this.currentPage = this.$store.getters["local_repository/currentPage"];
      this.pageSize = this.$store.getters["local_repository/pageSize"];
    },
  },

  mounted() {
    this.pageModelToView();
    this.initOpenWithList();
    this.fetch();
  },
};
</script>

<style scoped>
.pager-box {
  display: flex;
  flex-direction: row;
  justify-content: center;
  align-items: center;
  margin-top: 10px;
  margin-bottom: 50px;
}
</style>

