<template>
  <div title="Session Group Status">
    <el-button> S </el-button>
  </div>
</template>

<script>
export default {
  name: "session-group-status",

  methods: {
    checkQuery() {
      let dsid = this.$route.query["dsid"];
      if (dsid == null) {
        return;
      }
      console.log("find desktop-session-id:" + dsid);
      this.$store.commit("storage/dsid", dsid);
    },
  },

  mounted() {
    this.checkQuery();
  },
};
</script>
