

const use_root = { root: true }



const state = {

    intents: []

};

const getters = {

    list(state) {
        return state.intents
    }

}

const mutations = {

    handle_intents(state, vo) {
        if (vo == null) { return }
        let intents = vo.intents
        if (intents == null) { return }
        state.intents = intents
    }

};

const actions = {
    fetch(context) {
        let method = 'GET'
        let type = 'intents'
        let task = context.dispatch("rest/execute", { method, type }, use_root)
        task.then((r) => {
            let vo = r.data;
            context.commit('handle_intents', vo)
        })
        return task;
    },

    run(context, vo) {
        let method = 'POST'
        let type = 'intents'
        let data = vo;
        let task = context.dispatch("rest/execute", { method, type, data }, use_root)
        task.then((r) => {
            let vo = r.data;
            context.commit('handle_intents', vo)
        })
        return task;
    }

};



export default {
    name: "module_intent",
    namespaced: true,
    state, mutations, actions, getters
}
