<template>
  <div>
    <!-- header -->
    <MyHeader :config="theConfig" @action="handleAction" />

    <!-- search-box -->
    <MySearchBox
      :config="theConfig"
      v-show="theDisplaySearch"
      @action="handleAction"
    />

    <!-- body -->
    <slot> </slot>

    <!-- pager -->
    <MyPager v-show="theDisplayPager" />

    <!-- footer -->
    <MyFooter />

    <MyDebug v-show="theDisplayDebug" />
  </div>
</template>

<script>
import MyHeader from "./header.vue";
import MySearchBox from "./home-frame/search-box.vue";
import MyPager from "./home-frame/pager.vue";
import MyFooter from "./footer.vue";

import MyDebug from "./../utils/debug.vue";

export default {
  name: "home-frame",

  components: { MyHeader, MySearchBox, MyPager, MyFooter, MyDebug },

  props: {
    config: Object,
  },

  computed: {
    theConfig() {
      let c = this.config;
      if (c == null) {
        c = {
          pager: true,
          home: true,
          nav: true,
          search: true,
          searchCategory: "",
        };
      }
      return c;
    },

    theDisplayPager() {
      return this.theConfig.pager ? true : false;
    },

    theDisplaySearch() {
      return this.theConfig.search ? true : false;
    },

    theDisplayDebug() {
      return this.theConfig.debug ? true : false;
    },
  },

  methods: {
    handleAction(p) {
      this.$emit("action", p);
    },
  },
};
</script>
