<template>
  <div class="debug">
    <div>timer(1hz) : {{ t1 }}</div>
    <div>timer(5hz) : {{ t5 }}</div>

    <MyTimer :enabled="true" :interval="1000" @timer="handleTimer1hz" />
    <MyTimer :enabled="true" :interval="5000" @timer="handleTimer5hz" />
  </div>
</template>

<script>
import MyTimer from "./timer.vue";

export default {
  name: "debug",

  components: { MyTimer },

  data() {
    return {
      t1: 0,
      t5: 0,
    };
  },

  methods: {
    handleTimer1hz() {
      this.t1++;
    },

    handleTimer5hz() {
      this.t5++;
    },
  },
};
</script>

<style scoped>
.debug {
  background-color: yellow;
  border-width: 10px;
  border-color: black;
  border-style: solid;
  padding: 10px;
  color: gray;
}
</style>
