

import StateJS from './../js/state'


const use_root = { root: true }

function nop() { }

////////////////////////////////////////////////////////////////////////////////

const state = {
    repositories: []
};

const state_rw = StateJS.createStateRW({
    listname: "local_repositories",
    pk: "path",
})
state_rw.init(state)
state_rw.setPage(state, { limit: 999, offset: 0 })


const getters = {
    list(state) {
        return state_rw.listAll(state)
    }
}

const mutations = {

    handle_search_result(state, vo) {
        state_rw.updateVO(state, vo)
    },

    handle_result_repositories(state, vo) {
        state_rw.updateVO(state, vo)
    },

    reset(state) {
        state_rw.reset(state)
    },

};

const actions = {
    search(context, p) {

        let path = p.path

        let method = 'POST'
        let type = 'import-repositories'
        let data = {
            action: "find",
            local_repositories: [{ path }]
        }
        context.commit('reset')
        return context.dispatch("rest/execute", { method, type, data }, use_root).then((r) => {
            let vo = r.data;
            context.commit('handle_search_result', vo)
        })
    },

    locate(context, p) {

        let path = p.path

        let method = 'POST'
        let type = 'import-repositories'
        let data = {
            action: "locate",
            local_repositories: [{ path }]
        }
        context.commit('reset')
        return context.dispatch("rest/execute", { method, type, data }, use_root).then((r) => {
            let vo = r.data;
            context.commit('handle_search_result', vo)
        })
    },

    save(context, { items }) {

        nop(items)

        let method = 'POST'
        let type = 'import-repositories'
        let data = {
            action: "save",
            local_repositories: items
        }
        let task = context.dispatch("rest/execute", { method, type, data }, use_root)
        task.then((r) => {
            let vo = r.data;
            context.commit('handle_result_repositories', vo)
        })
        return task;
    },
};



export default {
    name: "module_import_local_repository",
    namespaced: true,
    state, mutations, actions, getters
}
