<template>
  <HomeFrame :config="config">
    <div>
      <h1>(repo-name)</h1>

      <el-tabs
        v-model="activeName"
        class="demo-tabs"
        @tab-change="handleChange"
      >
        <el-tab-pane label="Overview" name="Overview"> </el-tab-pane>
        <el-tab-pane label="Worktrees" name="Worktrees"> </el-tab-pane>
        <el-tab-pane label="Remotes" name="Remotes"> </el-tab-pane>
        <el-tab-pane label="Commits" name="Commits"> </el-tab-pane>
        <el-tab-pane label="Tags" name="Tags"> </el-tab-pane>
      </el-tabs>

      <slot> </slot>
    </div>
  </HomeFrame>
</template>
  
<script>
function makeConfig() {
  return {
    pager: false,
    home: true,
    nav: true,
    search: false,
    searchCategory: "",
  };
}

export default {
  name: "local-repository-frame",

  data() {
    let config = makeConfig();
    return {
      activeName: "",
      config,
    };
  },

  methods: {
    handleChange(name) {
      console.log("tab-click: " + name);
    },
  },
};
</script>
  