

// const use_root = { root: true }

////////////////////////////////////////////////////////////////////////////////

function Property(storage, name, def) {
    this.storage = storage;
    this.name = name;
    this.defaultValue = def;
}

Property.prototype = {

    get() {
        let value = this.storage[this.name];
        if (value == null) {
            value = this.defaultValue;
        }
        return value;
    },

    set(value) {
        if (value == null) { return }
        this.storage[this.name] = value;
        return value;
    },

}

////////////////////////////////////////////////////////////////////////////////

let local = window.localStorage
let session = window.sessionStorage


const P_JWT = new Property(session, "jwt", "")

const P_DESKTOP_SESSION_ID = new Property(session, "desktop_session_id", "")

const P_EXAMPLE = new Property(local, "example", 0)

////////////////////////////////////////////////////////////////////////////////

const state = {
};

const getters = {
    jwt() {
        return P_JWT.get()
    },

    dsid() {
        return P_DESKTOP_SESSION_ID.get()
    },
}

const mutations = {

    example(state, value) {
        state.example = P_EXAMPLE.set(value)
    },

    jwt(state, value) {
        state.jwt = P_JWT.set(value)
    },

    dsid(state, value) {
        state.sgid = P_DESKTOP_SESSION_ID.set(value)
    },
};

const actions = {
};


export default {
    name: "module_web_storage",
    namespaced: true,
    state, mutations, actions, getters
}
