<template>
  <div></div>
</template>
  
<script>
export default {
  name: "timer",

  computed: {
    t1hz() {
      return this.$store.getters["timer/timer1hz"];
    },
  },

  data() {
    return {
      current: 0, //  1hz value 
      count: 0, // 
    };
  },

  methods: {
    fireOnTimer() {
      this.$emit("timer");
    },

    handle1hz(nextValue) {
      if (!this.enabled) {
        return;
      }
      let preValue = this.current;
      let diff = (nextValue - preValue) * 1000;
      this.count += diff;
      this.current = nextValue;
      let step = this.interval;
      let timeout = 5;
      for (; step < this.count; timeout--) {
        if (timeout < 0) {
          this.count = 0;
          break;
        }
        this.count -= step;
        this.fireOnTimer();
      }
    },
  },

  props: {
    enabled: Boolean,
    interval: Number,
  },

  watch: {
    t1hz(value) {
      this.handle1hz(value);
    },
  },
};
</script>
  