<template>
  <div>
    <div class="s-box">
      <el-input
        v-model="text"
        clearable
        @input="fireApplyFilterAction"
        placeholder=""
      >
        <template #prepend> {{ theSearchCategory }} </template>
        <template #append>
          <el-button title="">
            <el-icon><Search /></el-icon>
          </el-button>
        </template>
      </el-input>
    </div>
  </div>
</template>

<script setup>
import { Search } from "@element-plus/icons-vue";
</script>

<script>
export default {
  name: "home-frame-search-box",

  props: {
    config: Object,
  },

  data() {
    return {
      text: "",
    };
  },

  computed: {
    theConfig() {
      let c = this.config;
      if (c == null) {
        c = {};
      }
      return c;
    },

    theSearchCategory() {
      let text = this.theConfig.searchCategory;
      if (text == null || text == "") {
        text = "";
      }
      return text;
    },
  },

  methods: {
    fireApplyFilterAction(value) {
      let p = { name: "applyFilter", value };
      this.$emit("action", p);
    },
  },
};
</script>

<style scoped>
.s-box {
  max-width: 640px;
  padding-top: 55px;
  padding-bottom: 55px;
  margin-left: auto;
  margin-right: auto;
}
</style>
