

// const use_root = { root: true }


////////////////////////////////////////////////////////////////////////////////

function Timer(ctx) {
    this.context = ctx;
    this.count = 0;
}

Timer.started = false

Timer.prototype = {

    hasStarted() {
        return Timer.started
    },

    start() {
        if (this.hasStarted()) {
            return
        }
        Timer.started = true
        this.setupForNextEvent()
    },

    setupForNextEvent() {
        setTimeout(() => { this.onTimer() }, 1000)
    },

    onTimer() {
        this.count++;
        let cnt = this.count;
        this.context.commit('update1hz', cnt)
        this.setupForNextEvent()
    },

}

////////////////////////////////////////////////////////////////////////////////

const state = {
    count1hz: 0
};

const getters = {
    timer1hz(state) {
        return state.count1hz
    }
}

const mutations = {
    update1hz(state, value) {
        if (value == null) { return }
        state.count1hz = value
    }
};

const actions = {
    init(context) {
        let t = new Timer(context);
        if (!t.hasStarted()) {
            t.start()
        }
    },
};


export default {
    name: "module_timer",
    namespaced: true,
    state, mutations, actions, getters
}
