

// import ItemSelector from './item_selector'


const use_root = { root: true }



function nop() { }


////////////////////////////////////////////////////////////////////////////////


const state = {
    all: [],
};

const getters = {

    all(state) {
        return state.all
    },

}

const mutations = {
    update(state, p) {
        nop(state, p)
    },
};

const actions = {
    fetch(context, id) {
        nop(context, id, use_root)
    },

    update(context, item) {
        nop(context, item)
    },

    remove(context, item) {
        nop(context, item)
    },
};


export default {
    name: "module_repository_detail",
    namespaced: true,
    state, mutations, actions, getters
}
