
import axios from 'axios'


function nop() { }



axios.interceptors.request.use(function (config) {
    // console.log("axios.interceptors.request.use")
    return config;
}, function (error) {
    return Promise.reject(error);
});

axios.interceptors.response.use(function (response) {
    // console.log("axios.interceptors.response.use")
    return response;
}, function (error) {
    return Promise.reject(error);
});

////////////////////////////////////////////////////////////////////////////////


function Request(ctx) {
    nop(ctx)
}


Request.prototype = {

    forAxios() {
        let p = {}
        this.forAxiosField(p, "method")
        this.forAxiosField(p, "params")
        this.forAxiosField(p, "data")
        this.forAxiosField(p, "url")
        return p;
    },

    forAxiosField(p, name) {
        let value = this[name];
        if (value == null) {
            return;
        }
        p[name] = value;
    },

    resolve(r) { nop(r) },

    reject(r) { nop(r) },

}


////////////////////////////////////////////////////////////////////////////////

function RequestBuilder(ctx) {
    nop(ctx)
}


RequestBuilder.prototype = {

    init(p) {
        this.method = p.method;
        this.url = p.url;
        this.params = p.params;
        this.data = p.data;
        this.type = p.type;
        this.id = p.id;
    },

    create() {
        let req = new Request();
        req.method = this.makeMethod();
        req.url = this.makeURL();
        if (this.params != null) {
            req.params = this.params;
        }
        if (this.data != null) {
            req.data = this.data;
        }
        return req;
    },

    makeMethod() {
        let method = this.method;
        if (method == null) {
            method = "GET"
        }
        return method;
    },

    makeURL() {
        let url = this.url;
        let type = this.type;
        let id = this.id;
        if (url == null) {
            url = this.getBaseRestURL() + type
            if (id != null) {
                url += ("/" + id)
            }
        }
        return url
    },

    getBaseRestURL() {
        return "/api/v1/"
    },

}




////////////////////////////////////////////////////////////////////////////////

const state = {
};

const getters = {
}

const mutations = {
};

const actions = {

    execute(context, p) {
        let rb = new RequestBuilder(context);
        rb.init(p);
        let task = rb.create()
        let promise = axios(task.forAxios())
        promise.then((r) => {
            task.resolve(r);
            return Promise.resolve(r)
        }).catch((r) => {
            task.reject(r)
            return Promise.reject(r)
        })
        return promise;
    }

};


export default {
    name: "module_rest",
    namespaced: true,
    state, mutations, actions, getters
}
