<template>
  <div class="header-bar">
    <!-- <div>WPM</div> -->

    <div v-show="theDisplayHome">
      <el-button link @click="open('/')" title="">
        <el-icon><HomeFilled /></el-icon>
      </el-button>
    </div>

    <div v-show="theDisplayNav" class="nav-items">
      <el-button link @click="open('/repositories')"></el-button>
      <el-button link @click="open('/remotes')" v-show="theDevDebug"
        ></el-button
      >
      <el-button link @click="open('/executables')"></el-button>
      <el-button link @click="open('/projects')" v-show="theDevDebug"
        ></el-button
      >
    </div>

    <div class="spring-gap"></div>

    <MySessionGroupStatus v-show="false" />

    <el-button link title="" @click="fireAction('refresh')">
      <el-icon><Refresh /></el-icon>
    </el-button>

    <el-button link title="" @click="fireAction('add')" v-show="false">
      <el-icon><Plus /></el-icon>
    </el-button>

    <el-button link @click="open('/settings')" title="">
      <el-icon><Setting /></el-icon>
    </el-button>
  </div>
</template>

<script setup>
import { HomeFilled, Plus, Refresh, Setting } from "@element-plus/icons-vue";
</script>


<script>
import MySessionGroupStatus from "./session-group-status.vue";

export default {
  name: "home-frame-header",

  components: { MySessionGroupStatus },

  props: {
    config: Object,
  },

  computed: {
    theConfig() {
      let c = this.config;
      if (c == null) {
        c = {};
      }
      return c;
    },

    theDisplayHome() {
      return this.theConfig.home ? true : false;
    },

    theDisplayNav() {
      return this.theConfig.nav ? true : false;
    },

    theDevDebug() {
      return this.$store.getters["dev/debug"];
    },
  },

  methods: {
    open(url) {
      this.$router.push(url);
    },

    fireAction(name) {
      let params = { name };
      this.$emit("action", params);
    },
  },
};
</script>

<style scoped>
.header-bar {
  display: flex;
  flex-direction: row;
  padding: 12px;
}

.spring-gap {
  flex-grow: 1;
}

.nav-items {
  margin-left: 10px;
}
</style>
