

////////////////////////////////////////////////////////////////////////////////


function ItemUpdater() {
    this.pkname = "undef";
    this.srclist = []
    this.dstlist = []
}


ItemUpdater.prototype = {

    pk(name) {
        if (name != null) {
            this.pkname = name
        }
        return this
    },

    src(list) {
        if (list != null) {
            this.srclist = list
        }
        return this
    },

    dst(list) {
        if (list != null) {
            this.dstlist = list
        }
        return this
    },

    makeSourceTable(list) {
        let dst = {}
        let src = list;
        for (var i in src) {
            let item = src[i]
            let key = this.getItemKey(item);
            dst[key] = item;
        }
        return dst;
    },

    getItemKey(item) {
        let name = this.pkname;
        if (item == null || name == null) {
            return "";
        }
        let key = item[name]
        if (key == null) {
            return "";
        }
        return key
    },


    updateItemValues(dst_item, src_item) {
        if (dst_item == null || src_item == null) {
            return
        }
        for (var fname in src_item) {
            var fval = src_item[fname];
            if (fval == null) {
                continue
            }
            dst_item[fname] = fval;
        }
    },


    update() {

        let src = this.srclist;
        let dst = this.dstlist;
        let table = this.makeSourceTable(src)

        // for dst (do update olders) 
        for (var i in dst) {
            let old_item = dst[i]
            let key = this.getItemKey(old_item)
            let new_item = table[key]
            if (new_item != null) {
                this.updateItemValues(old_item, new_item);
                table[key] = null;
            }
        }


        // for src (do insert newers)
        for (i in src) {
            let item1 = src[i]
            let key = this.getItemKey(item1)
            let item2 = table[key]
            if (item1 != null && item2 != null) {
                dst.push(item1)
            }
        }
    },

    remove(id) {
        let src = this.srclist;
        let dst = this.dstlist;
        for (var i in src) {
            let item = src[i]
            let key = this.getItemKey(item)
            if (id == key) {
                continue
            }
            dst.push(item)
        }
    },

}

////////////////////////////////////////////////////////////////////////////////


function FilterResultsBuilder(state, rw) {
    this.state = state;
    this.rw = rw;
}

FilterResultsBuilder.prototype = {

    build() {
        let state = this.state;
        let src = this.rw.listAll(state)
        let dst = []

        // todo ...
        dst = src;

        state.filterResults = dst
    }
}


////////////////////////////////////////////////////////////////////////////////

function CurrentPageBuilder(state, rw) {
    this.state = state;
    this.rw = rw;
}

CurrentPageBuilder.prototype = {

    build() {
        let state = this.state;
        let offset = state.offset;
        let limit = state.limit;
        let src = this.rw.listFilterResults(state)
        let dst = []
        let end1 = src.length;
        let end = offset + limit;
        if (end > end1) {
            end = end1
        }
        for (var i = offset; i < end; i++) {
            let item = src[i];
            dst.push(item)
        }
        state.currentPageItems = dst
    }

}

////////////////////////////////////////////////////////////////////////////////

function StateRW(config) {
    this.listname = config.listname; // vo 
    this.pk = config.pk; // 
}

StateRW.prototype = {

    init(state) {
        this.reset(state)
    },

    remove(state, id) {

        if (state == null || id == null) {
            return
        }

        let src = state.all;
        let dst = [];

        let iu = new ItemUpdater();
        iu.pk(this.pk).src(src).dst(dst).remove(id)

        state.all = dst;

        state.wantRebuildCurrentPageItems = true
        state.wantRebuildFilterResults = true
    },

    updateOne(state, item) {

        if (state == null || item == null) {
            return
        }

        let src = [item];
        let dst = state.all;

        let iu = new ItemUpdater();
        iu.pk(this.pk).src(src).dst(dst).update()

        state.wantRebuildCurrentPageItems = true
        state.wantRebuildFilterResults = true
    },

    updateList(state, list) {

        if (state == null || list == null) {
            return
        }

        let src = list;
        let dst = state.all;

        let iu = new ItemUpdater();
        iu.pk(this.pk).src(src).dst(dst).update()

        state.wantRebuildCurrentPageItems = true
        state.wantRebuildFilterResults = true
    },

    updateVO(state, vo) {

        if (state == null || vo == null) {
            return
        }

        let listname = this.listname;
        let src = vo[listname];
        let dst = state.all;

        let iu = new ItemUpdater();
        iu.pk(this.pk).src(src).dst(dst).update()

        state.wantRebuildCurrentPageItems = true
        state.wantRebuildFilterResults = true
    },

    setPage(state, p) {

        let limit = p.limit
        let offset = p.offset
        let current = p.current // the page-number base on 1

        if (limit != null) {
            state.limit = limit
        } else {
            limit = state.limit
        }

        if (offset != null) {
            state.offset = offset;
        }

        if (current != null) {
            let pageidx = current - 1;
            state.offset = pageidx * limit;
        }

        state.wantRebuildCurrentPageItems = true
    },

    setFilter(state, p) {

        let text = p.text
        let fn = p.fn

        if (text != null) {
            state.filterText = text
        }

        if (fn != null) {
            state.filterFn = fn
        }

        state.wantRebuildCurrentPageItems = true
        state.wantRebuildFilterResults = true
    },

    listAll(state) {
        return state.all
    },

    listFilterResults(state) {
        if (state.wantRebuildFilterResults) {
            state.wantRebuildFilterResults = false
            let builder = new FilterResultsBuilder(state, this);
            builder.build()
        }
        return state.filterResults
    },

    listCurrentPage(state) {
        if (state.wantRebuildCurrentPageItems) {
            state.wantRebuildCurrentPageItems = false
            let builder = new CurrentPageBuilder(state, this);
            builder.build()
        }
        return state.currentPageItems
    },

    currentPageNumber(state) {
        return this.currentPageIndex(state) + 1
    },

    currentPageIndex(state) {
        let offset = state.offset;
        let limit = state.limit;
        if (limit > 0.5 && offset > 0) {
            return Math.floor(offset / limit)
        }
        return 0
    },

    offset(state) {
        return state.offset
    },

    limit(state) {
        return state.limit
    },


    reset(state) {
        state.all = []
        state.filterResults = []
        state.currentPageItems = []

        state.limit = 10  // aka page-size
        state.offset = 0

        state.filterText = ""
        state.filterFn = () => { }

        state.wantRebuildFilterResults = true;
        state.wantRebuildCurrentPageItems = true;
    },

}


////////////////////////////////////////////////////////////////////////////////

export default {
    name: "state",

    createStateRW(config) {
        return new StateRW(config);
    }
}
