

const use_root = { root: true }



const state = {

    projects: []

};

const getters = {

    list(state) {
        return state.projects
    }

}

const mutations = {

    handle_projects(state, vo) {
        if (vo == null) { return }
        let projects = vo.projects
        if (projects == null) { return }
        state.projects = projects
    }

};

const actions = {
    fetch(context) {
        let method = 'GET'
        let type = 'projects'
        let task = context.dispatch("rest/execute", { method, type }, use_root)
        task.then((r) => {
            let vo = r.data;
            context.commit('handle_projects', vo)
        })
        return task;
    },
};



export default {
    name: "module_project",
    namespaced: true,
    state, mutations, actions, getters
}
