<template>
  <div class="item">
    <div class="item-title-row">
      <div class="item-name" @click="handleClickItemName">{{ data.name }}</div>
      <div class="gap" />
      <MyOpenWithList @action="handleAction" />
    </div>
    <div class="item-description">{{ data.description }}</div>
    <div class="item-path" @click="handleClickItemPath">{{ data.path }}</div>
  </div>
</template>

<script>
// import MyDetail from "./item-detail.vue";
// import MyOverview from "./item-overview.vue";
import MyOpenWithList from "./open-with-list.vue";

export default {
  name: "home-local-repository-item",

  components: { MyOpenWithList },

  computed: {
    apps() {
      return this.$store.getters["executable/openWithList"];
    },
  },

  methods: {
    handlePopoverShow() {
      let id = this.data.id;
      console.warn("todo: load-local-repo-detail:" + id);
    },

    handleClickItemName() {
      // this.openWithDefaultExe();
      let id = 0;
      this.$router.push("/repositories/" + id);
    },

    handleClickItemPath() {
      this.openWithDefaultExe();
    },

    handleAction(e) {
      let name = e.name;
      if (name == "open-with") {
        this.openWith(e);
      }
    },

    openWithDefaultExe() {
      let apps = this.apps;
      let e = {};
      for (var i in apps) {
        let app = apps[i];
        if (app != null) {
          e.executable = app;
          this.openWith(e);
          break;
        }
      }
    },

    openWith(e) {
      e.repository = this.data;
      // console.log("do-open-with, exe_id:" + eid + " repo_id:" + rid);
      let intent = {
        executable: e.executable,
        repository: e.repository,
      };
      let vo = {
        intents: [intent],
      };
      this.$store.dispatch("intent/run", vo);
    },
  },

  props: {
    data: Object,
  },
};
</script>

<style scoped>
.item {
  padding: 10px;
  text-align: left;
  width: 100%;
}

.item-name {
  font-size: 20px;
  color: black;
  margin-bottom: 5px;
  cursor: pointer;
}

.item-name:hover {
  color: blue;
}

.item-path {
  font-size: 12px;
  color: rgb(168, 168, 168);
  cursor: pointer;
  margin-top: 5px;
}

.item-path:hover {
  color: blue;
}

.item-description {
  font-size: 12px;
  color: gray;
}

.item-title-row {
  display: flex;
  flex-direction: row;
}

.gap {
  flex-grow: 1;
}
</style>
