
import ItemSelector from './item_selector'


const use_root = { root: true }




function makeOpenWithList(srclist) {
    if (srclist == null) { srclist = [] }
    const valueForItem = function (item) {
        if (item == null) { return 0 }
        let v = item.open_with_priority;
        if (v == null) { return 0 }
        return v;
    }
    srclist.sort((a, b) => {
        let v1 = valueForItem(a)
        let v2 = valueForItem(b)
        return (v2 - v1);
    })
    let dstlist = []
    for (var i in srclist) {
        let item = srclist[i]
        let value = valueForItem(item)
        if (value > 0) {
            dstlist.push(item)
        } else {
            break
        }
    }
    return dstlist
}




////////////////////////////////////////////////////////////////////////////////

function StateUpdater(state) {
    this.state = state;
}

StateUpdater.prototype = {

    update(p) {
        if (p == null) { return; }
        this.updateVO(p.vo)
        this.updateFilter(p.filter)
        this.updatePageIndex(p.index)
        this.updatePageSize(p.size)
    },

    updateVO(vo) {
        if (vo == null) { return; }
        let exelist = vo.executables
        if (exelist == null) { return }
        this.state.all = exelist
        this.state.openWithList = makeOpenWithList(exelist)
    },

    updatePageSize(value) {
        if (value == null) { return; }
        this.state.pageSize = value;
    },

    updatePageIndex(value) {
        if (value == null) { return; }
        this.state.pageIndex = value;
    },

    updateFilter(value) {
        if (value == null) { return; }
        this.state.filter = value.trim();
    },

}

////////////////////////////////////////////////////////////////////////////////

function Filter(state) {
    this.state = state;
}


Filter.prototype = {

    apply(text) {
        let src = this.state.all;
        let dst = this.doFilter(src, text)
        this.state.afterFilter = dst;
    },

    doFilter(src, text) {
        text = this.normalizeFilterText(text);
        if (text == "") {
            return src
        }
        let dst = []
        for (var i in src) {
            let item = src[i];
            if (this.acceptItem(item, text)) {
                dst.push(item)
            }
        }
        return dst;
    },

    acceptItem(item, filter) {
        if (this.containString(filter, item.name)) { return true }
        if (this.containString(filter, item.title)) { return true }
        if (this.containString(filter, item.description)) { return true }
        // if (this.containString(filter, item.path)) { return true }
        return false;
    },

    containString(keyword, text) {
        if ((keyword == null) || (text == null)) { return false }
        let i = text.indexOf(keyword)
        return (i >= 0)
    },

    normalizeFilterText(text) {
        if (text == "") { return "" }
        return text.trim()
    }
}


////////////////////////////////////////////////////////////////////////////////



const state = {

    currentPageItems: [],
    executables: [],
    openWithList: [],

    all: [],
    afterFilter: [],
    viewport: [],
    filter: "",
    pageSize: 16,
    pageIndex: 0,
};

const getters = {

    openWithList(state) {
        return state.openWithList
    },

    all(state) {
        return state.all
    },

    currentPageItems(state) {
        return state.viewport
    },

    currentPage(state) {
        return (state.pageIndex + 1)
    },

    pageSize(state) {
        return state.pageSize
    },

    afterFilter(state) {
        return state.afterFilter
    },

    total(state) {
        return state.afterFilter.length
    },
}

const mutations = {

    removeFromState(state, id) {
        let src = state.all
        let dst = []
        for (var i in src) {
            let item = src[i];
            if (item.id == id) {
                continue
            }
            dst.push(item)
        }
        state.all = dst;
    },

    update(state, p) {
        let up = new StateUpdater(state);
        up.update(p)

        let fl = new Filter(state);
        fl.apply(state.filter)

        let vpt = ItemSelector.select(state.afterFilter, state.pageIndex, state.pageSize)
        state.viewport = vpt
    },

};

const actions = {

    init(context) {
        context.dispatch("fetch")
    },

    fetch(context) {
        let method = 'GET'
        let type = 'executables'
        let p = { method, type }
        return context.dispatch("rest/execute", p, use_root)
            .then((r) => {
                let vo = r.data;
                context.commit('update', { vo })
            })
    },

    insert(context, item) {
        let method = 'POST'
        let type = 'executables'
        let data = { executables: [item] }
        let p = { method, type, data }
        return context.dispatch("rest/execute", p, use_root)
            .then(() => {
                context.dispatch('fetch')
            })
    },

    update(context, item) {
        let method = 'PUT'
        let type = 'executables'
        let data = { executables: [item] }
        let id = item.id;
        let p = { method, type, id, data }
        return context.dispatch("rest/execute", p, use_root)
            .then(() => {
                context.dispatch('fetch')
            })
    },

    prepareInsert(context, item) {
        let method = 'POST'
        let type = 'import-executables'
        let data = { executables: [item] }
        let p = { method, type, data }
        return context.dispatch("rest/execute", p, use_root)
    },

    remove(context, id) {
        let method = 'DELETE'
        let type = 'executables'
        let data = {};
        let p = { method, type, id, data }
        return context.dispatch("rest/execute", p, use_root)
            .then(() => {
                context.dispatch('fetch')
            })
    }
};


export default {
    name: "module_executable",
    namespaced: true,
    state, mutations, actions, getters
}
