import { createRouter, createWebHistory } from 'vue-router'
import Home from '../views/Home.vue'

const routes = [
  {
    path: '/',
    name: 'Home',
    component: Home
  },

  // at home
  { path: "/repositories", component: () => import('../views/HomeLocalRepositories.vue') },
  { path: "/repositories/:id", component: () => import('../views/LocalRepositoryView.vue') },
  { path: "/repositories/:id/worktrees", component: () => import('../views/LocalRepositoryView.vue') },
  { path: "/repositories/:id/commits", component: () => import('../views/LocalRepositoryView.vue') },
  { path: "/repositories/:id/tags", component: () => import('../views/LocalRepositoryView.vue') },
  { path: "/repositories/:id/tags/:tagid", component: () => import('../views/LocalRepositoryView.vue') },

  { path: "/remotes", component: () => import('../views/HomeRemoteRepositories.vue') },
  { path: "/executables", component: () => import('../views/HomeExecutables.vue') },
  { path: "/projects", component: () => import('../views/HomeProjects.vue') },

  // import
  { path: "/import-local-repo", component: () => import('../views/ImportLocalRepo') },
  { path: "/import-local-repository", component: () => import('../views/ImportLocalRepository') },
  { path: "/import-local-repositories", component: () => import('../views/ImportLocalRepositories') },

  // settings
  { path: "/settings", component: () => import('../views/Settings.vue') },
  { path: "/settings/projects", component: () => import('../views/ProjectView.vue') },
  { path: "/settings/repositories", component: () => import('../views/SettingsLocalRepositories.vue') },
  { path: "/settings/remotes", component: () => import('../views/SettingsRemoteRepositories.vue') },
  { path: "/settings/executables", component: () => import('../views/SettingsExecutables') },
  { path: "/settings/intents", component: () => import('../views/IntentView.vue') },

  {
    path: '/about',
    name: 'About',
    component: () => import(/* webpackChunkName: "about" */ '../views/About.vue')
  }
]


function prepareRoutes(list) {
  for (var i in list) {
    let item = list[i]
    let name = item.name
    if (name == null) {
      item.name = item.path;
    }
  }
}

prepareRoutes(routes)


const router = createRouter({
  history: createWebHistory(process.env.BASE_URL),
  routes
})

export default router
