import { createApp } from 'vue'

import ElementPlus from 'element-plus'
import 'element-plus/dist/index.css'
import zhCn from 'element-plus/dist/locale/zh-cn.mjs'

import App from './App.vue'
import router from './router'
import store from './store'

import AdminFrame from './components/frame/AdminFrame'
import SimpleFrame from './components/frame/SimpleFrame'
import HomeFrame from './components/frame/HomeFrame'
import RepositoryFrame from './components/frame/RepositoryFrame'


const app = createApp(App)

app.component('AdminFrame', AdminFrame)
app.component('SimpleFrame', SimpleFrame)
app.component('HomeFrame', HomeFrame)
app.component('RepositoryFrame', RepositoryFrame)

app.use(store).use(router)
app.use(ElementPlus, { locale: zhCn })

app.mount('#app')
