<template>
  <div>
    <el-menu
      :default-active="currentActive"
      class="el-menu-vertical-demo"
      :collapse="isCollapse"
      router
      @open="handleOpen"
      @close="handleClose"
    >
      <el-menu-item index="/settings" v-show="theDevDebug">
        <el-icon><document /></el-icon>
        <template #title>  </template>
      </el-menu-item>

      <el-menu-item index="/settings/repositories">
        <el-icon><List /></el-icon>
        <template #title>  </template>
      </el-menu-item>

      <el-menu-item index="/settings/remotes" v-show="theDevDebug">
        <el-icon><document /></el-icon>
        <template #title>  </template>
      </el-menu-item>

      <el-menu-item index="/settings/projects" v-show="theDevDebug">
        <el-icon><document /></el-icon>
        <template #title>  </template>
      </el-menu-item>

      <el-menu-item index="/settings/executables">
        <el-icon><Menu /></el-icon>
        <template #title>  </template>
      </el-menu-item>

      <el-menu-item index="/settings/intents" v-show="theDevDebug">
        <el-icon><Document /></el-icon>
        <template #title>  </template>
      </el-menu-item>

      <el-menu-item index="/about">
        <el-icon><InfoFilled /></el-icon>
        <template #title> </template>
      </el-menu-item>

      <el-menu-item index="/" v-show="theDevDebug">
        <el-icon><document /></el-icon>
        <template #title>  </template>
      </el-menu-item>
    </el-menu>
  </div>
</template>

<script setup>
import { Document, List, Menu, InfoFilled } from "@element-plus/icons-vue";
</script>

<script>
export default {
  name: "AdminNavMenu",

  computed: {
    theDevDebug() {
      return this.$store.getters["dev/debug"];
    },

    isCollapse() {
      return false;
    },

    currentActive() {
      let path = this.$route.path;
      return path;
    },
  },

  methods: {
    handleOpen() {},
    handleClose() {},
  },
};
</script>
