



function select(all, pageIndex, pageSize) {

    if (all == null) { all = []; }
    if (pageIndex == null) { pageIndex = 0; }
    if (pageSize == null) { pageSize = 16 }

    let offset = pageIndex * pageSize;
    let limit = pageSize;
    let end = all.length;
    let dst = [];
    let count = 0;
    for (let i = offset; i < end; i++) {
        if (count < limit) {
            let item = all[i];
            dst.push(item);
            count++;
        } else {
            break;
        }
    }
    return dst;
}



export default {
    name: "item_selector",
    select,
}
